/* eslint-disable no-unused-vars */
<template>
  <CRow class="justify-content-center">
    <CCol>
      <CCard>
        <CCardHeader class="d-flex justify-content-between">
          <div>
            <slot name="header">
              <CIcon name="cil-healing" />
              <strong>Suppliers</strong>
            </slot>
          </div>
          <CButton
            variant="outline"
            color="success"
            @click="openModalNewSupplier()"
          >
            <CIcon name="cil-plus" class="mt-0" />&nbsp;Add New Supplier
          </CButton>
        </CCardHeader>
        <CCardBody>
          <v-client-table :data="rows" :columns="columns" :options="options">
            <div slot="actions" slot-scope="{ row }">
              <CButton
                class="mr-3"
                variant="outline"
                @click="openModalEdit(row)"
                color="primary"
              >
                Edit
                <CIcon name="cil-pencil" />
              </CButton>
              <CButton
                class="mr-3"
                variant="outline"
                @click="deleteRow(row)"
                color="danger"
              >
                Delete
                <CIcon name="cil-delete" />
              </CButton>
            </div>
          </v-client-table>
        </CCardBody>
      </CCard>
    </CCol>
    <sweet-modal :blocking="Boolean(true)" ref="modal" overlay-theme="dark">
      <CCard style="text-align: left; border: 1px solid">
        <CCardHeader>
          <strong>New Supplier Form</strong>
        </CCardHeader>
        <CForm @submit.prevent="submit">
          <CCardBody>
            <p
              v-for="(error, index) in errors"
              :key="index"
              class="alert alert-danger"
            >
              {{ error }}
            </p>
            <CInput
              label="Supplier"
              :class="{ 'form-group--error': $v.supplier.$error }"
              v-model.trim="$v.supplier.$model"
            />
            <div class="error" v-if="!$v.supplier.required">
              Supplier is required
            </div>
            <CInput
              type="email"
              :class="{ 'form-group--error': $v.email.$error }"
              label="Email"
              v-model.trim="$v.email.$model"
            />
            <div class="error" v-if="!$v.email.required">
              Email is required
            </div>
            <div class="error" v-if="!$v.email.email">
              Please provide a valid email
            </div>
            <CRow>
              <CCol md="6">
                <CInput
                  type="phone"
                  :class="{ 'form-group--error': $v.contact.$error }"
                  label="Contact"
                  v-model.trim="$v.contact.$model"
                />
                <div class="error" v-if="!$v.contact.required">
                  Phone number is required
                </div>
              </CCol>
              <CCol md="6">
                <CInput
                  type="number"
                  step="any"
                  :class="{ 'form-group--error': $v.amount.$error }"
                  label="Due Amount"
                  v-model.trim="$v.amount.$model"
                />
                <div class="error" v-if="!$v.amount.required">
                  Amount is required
                </div>
              </CCol>
            </CRow>
          </CCardBody>
          <CCardFooter>
            <CButton
              class="col"
              variant="outline"
              color="success"
              type="submit"
            >
              <CIcon name="cil-check-circle" />&nbsp;Submit
            </CButton>
          </CCardFooter>
        </CForm>
      </CCard>
    </sweet-modal>
  </CRow>
</template>
<script>
import { required,email } from "vuelidate/lib/validators";
export default {
  name: "Suppliers",
  data() {
    return {
      supplier: "",
      email: "",
      contact: "",
      formData: {},
      rows: [],
      errors: [],
      isEdit: false,
      id: "",
      amount: "",
      columns: ["name", "phone", "email", "actions"],
      options: {
        columnsClasses: {
          actions: "text-center",
        },
        filterByColumn: true,
        perPage: 10,
        texts: {
          filter: "Filter:",
          filterBy: "Filter by {column}",
          count: " ",
        },
        filterable: ["name"],
        headings: {
          name: "Name",
        },
      },
    };
  },
  validations: {
    supplier: {
      required,
    },
    email: {
      required,
      email
    },
    contact: {
      required,
    },
    amount: {
      required,
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    goToBatches(row) {
      this.$router.push({ name: "Batches", params: { row: row } });
    },
    getData() {
      this.$http
        .get("http://localhost:3000/supplier/get")
        .then((res) => {
          this.rows = res.data;
          console.log(res.data);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    deleteRow(row) {
      this.$http
        .delete("http://localhost:3000/supplier/" + row._id)
        .then((res) => {
          this.getData();
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    submit() {
      this.$v.$touch()
      if (this.$v.$invalid) {
        this.errors.push("Please check the form and submit.");
      } else {
        this.formData = {
          name: this.supplier,
          email: this.email,
          phone: this.contact,
        };
        if (this.isEdit === false) {
          this.$http
            .post("http://localhost:3000/supplier/post", this.formData)
            .then((res) => {
              this.$swal.fire("Done!", "Supplier created successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        } else {
          this.$http
            .put("http://localhost:3000/supplier/" + this.id, this.formData)
            .then((res) => {
              this.$swal.fire("Done!", "Supplier edited successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        }
        this.$refs.modal.close("modal");
      }
    },
    async openModalEdit(row) {
      this.isEdit = true;
      this.id = row._id;
      this.$refs.modal.open("modal");
      this.supplier = row.name;
      this.email = row.email;
      this.contact = row.phone;
      this.errors = []
    },
    async openModalNewSupplier(row) {
      this.isEdit = false;
      this.$refs.modal.open("modal");
      this.supplier = "";
      this.email = "";
      this.contact = "";
      this.errors = []
    },
  },
};
</script>